<template>
<div class="app-page page-content-bottom-menu">
  <page-content class="">
     <div class="top-menu">
      <div class="left-img">
        <div @click="goPersonalData" class="img-container"><img class="user-img" src="/static/images/personal/logo.png"/></div>
      </div>
      <div class="center">
        <div class="username">用户名: 热心网友</div>
        <div @click="goAccountbalance" class="balance">账户余额: 2000元</div>
      </div>
      <div class="right-arrow"><img class="arrow" src="/static/images/personal/white-arrow.png"/></div>
    </div>
    <div class="blank-block"></div>
     <div class="cell-list">
          <div @click="go('/myearnings')" class="container">
            <div class="icon"><img class="my-benefit" src="/static/images/personal/sy.png" ></div>
            <div class="center-title">我的收益</div>
            <div class="right-arrow"><img src="/static/images/personal/black-arrow.png" /></div>
          </div>
          <div @click="go('/myadress')" class="container">
            <div class="icon"><img class="my-address" src="/static/images/personal/dz.png"/></div>
            <div class="center-title">我的地址</div>
            <div class="right-arrow"><img src="/static/images/personal/black-arrow.png" /></div>
          </div>
          <div @click="go('/autodistribution/list')" class="container">
            <div class="icon"><img class="auto-express" src="/static/images/personal/ps.png"/></div>
            <div class="center-title">自动配送管理</div>
            <div class="right-arrow"><img src="/static/images/personal/black-arrow.png" /></div>
          </div>
          <div @click="go('/vouchercenter')" class="container">
            <div class="icon"><img class="payin-center" src="/static/images/personal/cz.png"/></div>
            <div class="center-title">充值中心</div>
            <div class="right-arrow"><img src="/static/images/personal/black-arrow.png" /></div>
          </div>
          <div @click="go('/')" class="container">
            <div class="icon"><img class="online-person" src="/static/images/personal/kf.png"/></div>
            <div class="center-title">在线客服</div>
            <div class="right-arrow"><img src="/static/images/personal/black-arrow.png" /></div>
          </div>
        </div>
     <div class="my-order">
          <van-cell-group>
            <van-cell @click="goMyorder(0)" title="我的订单" is-link />
          </van-cell-group>
          <div class="order-status">
            <div @click="goMyorder(0)" class="status-list">
              <div class="content-img"><img class="waitpay-img" src="/static/images/personal/packet.png"/></div>
              <div class="bottom-title">待付款</div>
            </div>
            <div @click="goMyorder(0)" class="status-list">
              <div class="content-img"><img class="waitsend-img" src="/static/images/personal/send.png"/></div>
              <div class="bottom-title">待发货</div>
            </div>
            <div @click="goMyorder(0)" class="status-list">
              <div class="content-img"><img class="alreadyfinish-img" src="/static/images/personal/finish.png"/></div>
              <div class="bottom-title">已完成</div>
            </div>
            <div @click="goEvaluate(0)" class="status-list">
              <div class="content-img"><img class="wait-comment" src="/static/images/personal/s.png"/></div>
              <div class="bottom-title">待评价</div>
            </div>
            <div @click="goRefund(0)" class="status-list">
              <div class="content-img"><img class="return-pay" src="/static/images/personal/failed.png"/></div>
              <div class="bottom-title">退款</div>
            </div>
          </div>
        </div>
      <div class="banner">
          <div class="banner-container">
            <img src="/static/images/personal/banner.png"/>
          </div>
        </div>
  </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
export default {
  name: 'personalCenter',
  components: {
    PageContent
  },
  data () {
    return {
    }
  },
  methods: {
    go (url) {
      var id = 1
      this.$router.push({path: url, query: {id: id}})
    },
    goMyorder (type) {
      this.$router.push({path: '/order/myorder', query: {type: type}})
    },
    goAccountbalance () {
      var id = 1
      this.$router.push({path: '/accountbalance', query: {id: id}})
    },
    goPersonalData () {
      var id = 1
      this.$router.push({path: '/personalcenter/updPersonalData', query: {id: id}})
    },
    goEvaluate () {
      this.$router.push({ path: '/evaluate/list', query: {} })
    },
    goRefund () {
      this.$router.push({ path: '/refund/list', query: {} })
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import "@/styles/variables.scss";
  .page-content{
    background-color: $whitee6e6e6;
    .top-menu {
      background-image: url("/static/images/personal/bc.png");
      background-repeat: no-repeat;
      background-size: cover;
      display: flex;
      align-items: center;
      height: 20%;
      opacity: 0.8;
      width: 100%;
      .left-img{
        display: flex;
        width: 20%;
        align-items: center;
        justify-content: center;
        .img-container{
          .user-img{
            height: 114px;
            width: 114px;
          }
        }
      }
      .center{
        flex: 1;
        height: 114px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        .username{
          font-size: 30px;
          color: $whiteffffff;
          font-family: $font-family-default;
        }
        .balance{
          font-size: 30px;
          color: $whiteffffff;
          font-family: $font-family-default;
        }
      }
      .right-arrow{
        width: 15%;
        .arrow{
          height: 30px;
          width: 24px;
        }
      }
    }
    // 空白层站位
    .blank-block{
      height: 20%;
    }
    /*// 媒体查询*/
    /*@media screen and (min-width: 750px) {*/
      /*.blank-block{*/
        /*max-height: 12%;*/
      /*}*/
    /*}*/
    .container:nth-child(1){
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }
    .container:nth-last-child(1){
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
    }
    .cell-list{
      padding-top: 10px;
      display: flex;
      height: 40%;
      flex-direction: column;
      align-items: center;
      width: 100%;
      .container{
        display: flex;
        /*height: 18%;*/
        height: 120px;
        width: 90%;
        align-items: center;
        background-color: $whiteffffff;
        border-bottom: 1px solid $whitee6e6e6;
        .icon{
          width: 15%;
          text-align: center;
          .my-benefit{
            width: 34px;
            height: 38px;
          }
          .my-address{
            width: 34px;
            height: 37px;
          }
          .auto-express{
            width: 36px;
            height: 30px;
          }
          .payin-center{
            width: 37px;
            height: 37px;
          }
          .online-person{
            width: 35px;
            height: 40px;
          }
        }
        .center-title{
          font-size: 28px;
          color: $black666666;
          font-family: $font-family-default;
          flex: 1;
        }
        .right-arrow{
          width: 15%;
          text-align: center;
          img{
            width: 16px;
            height: 26px;
          }
        }
      }
    }
    .my-order {
      width: 100%;
      height: 22%;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 17%;
      /deep/ .van-hairline--top-bottom::after{
        /*border-bottom: 2px solid #ebedf0;*/
        border: 0;
      }
      .van-cell{
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
      }
      /deep/ .van-cell-group{
        width: 90%;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        border-bottom: 2px solid $whitee5e5e5;
      }
      .order-status{
        width: 90%;
        display: flex;
        /*height: calc(100% - 105px);*/
        height: 100%;
        justify-content: space-between;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
        display: flex;
        background-color: $whiteffffff;
        .status-list{
          /*width: 17%;*/
          width: 20%;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: center;
          text-align: center;
          .content-img{
            .waitpay-img{
              height: 46px;
              width: 46px;
            }
            .waitsend-img{
              height: 44px;
              width: 50px;
            }
            .alreadyfinish-img{
              height: 46px;
              width: 52px;
            }
            .wait-comment{
              height: 46px;
              width: 52px;
            }
            .return-pay{
              width: 59px;
              height: 46px;
            }
            .bottom-title{
              font-size: 24px;
              color: $black333333;
              font-family: $font-family-Medium;
            }
          }
        }
      }
    }
    .banner{
      margin-top: 20px;
      /*height: 20%;*/
      /*max-height: 22%;*/
      /*flex: 1;*/
      height: 17%;
      display: flex;
      justify-content: center;
      width: 100%;
      .banner-container{
        width: 90%;
        img{
          width: 100%;
          height: 100%;
          border-radius: 15px;
        }
      }
    }
    .container{
      flex-direction: row;
      margin: 0;
    }
  }
</style>



